.pdct {

  &-row {
    display: flex;
    justify-content: space-between
  }

  &-col {
    width: 48%;
  }

  @media (max-width: 1024px)  {

    &-row {
      flex-wrap: wrap;
    }

    &-col {
      width: 100%;
    }
  }
}



